import React, { useState } from 'react';
import './Login.css';
import Title from './components/Title/Title';
import Label from './components/Label/Label';
import Input from './components/Input/Input';

const Login = () => {

    const [ user, setUser ] = useState('');
    const [ password,setPassword] = useState('');
    const [ passwordError, setPasswordError ] = useState(false);
    const [ isLogin, setIsLogin] = useState(false);
    const [ hasError, setHasError] = useState(false);

    function handleChange(name,value) {
        if(name === 'usuario') {
            setUser(value);
            setHasError(false);
        } else {
            if(value.length < 6)
            {
                setPasswordError(true);
                setHasError(false)
            } else{
                setPasswordError(false);
                setPassword(value);
                setHasError(false);
            }
            
        }
    };

    function ifMatch(param) {
        if(param.user.length > 0 && param.password.length > 0 ){
            //para saber si hay base de datos 
            if(param.user === 'dimas' && param.password === '123456'){
                const { user, password} = param;
                let ac = { user, password }
                let account = JSON.stringify(ac);
                localStorage.setItem('account', account);
                setIsLogin(true);
                setHasError(false);
            } else
            {
                setIsLogin(false);
                setHasError(true);
            }
        } else {
            setIsLogin(false);
        }
    } 
    //console.log('usuario:',user)
    //console.log('passwod:',password)

    function handleSubmit (){
        let account = { user, password }
        if(account){
           //console.log('account =',account)
           ifMatch(account);
        }
    };

    return (
        
        <div className="login-container">
            {isLogin ? 
            <h1>
                Bienvenido, {user}
            </h1> :
            <div className="login-content">
                <Title text='LOGIN'/>
                {hasError &&
                    <label className="label-alert"> su contraseña o usuario son incorrectos,
                no existen en nuestra platafórma</label>}
                <Label text='Usuario'/>
                <Input 
                attribute={{
                    id: 'usuario',
                    name: 'usuario',
                    type: 'text',
                    placeholder:'Ingrese su usuario'
                }}
                handleChange={handleChange}
                />
                <Label text='Contraseña'/>
                <Input 
                attribute={{
                    id: 'contraseña',
                    name: 'contraseña',
                    type: 'password',
                    placeholder:'Ingrese su contraseña'
                }}
                handleChange={handleChange}
                param = {passwordError}
                />
                { passwordError &&
                    <label className="label-error">
                        contraseña inválida o incompleta
                    </label>
                }
                <div className="submit-button-container">

                <button onClick={handleSubmit} className="submit-button">
                    Ingresar
                </button>
                </div>
            </div>
            }
        </div>
        
    )
};

export default Login;